import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { ProfileAreaComponent } from './profile-area.component';
import { ProfileAreaBasicComponent } from './components/profile-area-basic/profile-area-basic.component';

const routes: Routes = [
	{ path: '', redirectTo: 'basic', pathMatch: 'full'},
	{ path: 'basic', component: ProfileAreaBasicComponent},
	{ path: '**', redirectTo: 'basic', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfileAreaRoutingModule { }
